import navigation from '../../components/navigation/navigation';
export default {
	data() {
		return {
			area:0,
			cardState:"close-card",
			markers: [],
			showmap:true,
			config: {
				type:1,//0无title，1有title
				title: "地图",
				back:true,
			},
			address: ''
		}
	},
	components: {
		navigation
	},
	methods: {
		customConduct() {
			uni.navigateBack({});
		},
		marktap(e){
			console.log(e.detail.latitude);
			console.log(e.detail.longitude);
			let longitude = e.detail.longitude;
			let latitude = e.detail.latitude;
			let _this = this
			uni.request({
			      url: 'https://restapi.amap.com/v3/geocode/regeo',
			      data: {
			        key: '53846ae53aa06f53c1a754a376673811',
			        location: longitude + "," + latitude,
			        extensions: "base",
			        s: "rsx",
			        sdkversion: "sdkversion",
			        logversion: "logversion"
			 
			      },
			      success: function (res) {
					  // console.log(res)
			    //     console.log(res.data.regeocode.formatted_address);
					_this.address = res.data.regeocode.formatted_address;
			      },
			      fail: function (res) {
			        console.log('获取地理位置失败')
			      }
			    })
			uni.showModal({
				title:'提示',
				content:'是否确定房源位置: '+_this.address,
				success:function(){
					_this.markers[0]={
						  iconPath: "../../static/markIcon.png",
					      id: 0,
					      latitude: latitude,
					      longitude: longitude,
					      width: 25,
					      height: 25
					}
					var pages = getCurrentPages();
					var prevPage = pages[pages.length - 2];
					prevPage.setData({
					     userdata: {
					       'address':_this.address,
						   'longitude':longitude,
						   'latitude':latitude
					     }
					})
					// uni.setStorageSync('latitude', latitude)
					// uni.setStorageSync('longitude', longitude)
					// uni.setStorageSync('address', _this.address)
					_this.showmap=false;
					setTimeout(() => {
					_this.showmap = true;
					}, 100);
				},
				fail:function(){
					console.log(1)
				}
			})
		},
		changearea1(){
			this.area = 0 ;
		},
		changearea2(){
			this.area = 1 ;
		},
		
	}
}
